import React,{Component} from 'react'
import './join-pre.css'
import join_01 from '../images/join_01.jpg'
import {Link} from 'react-router-dom'

class JoinPre extends Component{
  render(){
    return (
      <section className='join-pre'>
        <div className='joinpre'>
          <div className="joinpreup">
  					<div>
  						<p></p>
  					</div>
  				</div>
  				<div></div>
  				<div className='joinpreList'>
  					<div className="pic">
  						<a href="#">
  							<img src={join_01} alt="" />
  						</a>
  						<a href="#" className="words">
  						Boss T
  						</a>
  					</div>
  					<div className="sizes">
  						<span>iphone 5</span>
  						<span>XL</span>
  						<span>x1</span>
  					</div>
  					<div className="btn">
              <Link to='/'></Link>
              <Link to='/mozan/buycar'></Link>

  					</div>
  				</div>
        </div>
      </section>

    )
  }
}
export default JoinPre



